import React, {useState} from 'react';

function Form({text,addText,deleteText}){
    const [value, setValue] = useState('')
    
console.log(value)

    function handleChange(e){
        deleteText.current = false;
        setValue(value => e.target.value );
    }

    function newText(){
        if(value !== '') addText([...text, value])
        else alert('enter the text')
        setValue('');
    }
    
    return (

        <div className="page-one__form">
            <textarea value={value} placeholder="write something" onChange={(e)=>handleChange(e)} />
            <button className="pulse" onClick={newText}>add</button>
        </div>
    )
}

export default Form;